import { Heading, Text, VStack } from "@chakra-ui/react";
import { useContext, useEffect } from "react"
import { AlbumViewContext } from "../../../contexts/AlbumViewContext"

export default function Home() {

    const {mode} = useContext(AlbumViewContext);

    useEffect(()=>{
        mode.set('other');
    },[]);

    const questions : {q:string, a: string[]}[] = [
        {q: 'O que  o Polis?', a: [
            'O Polis  uma iniciativa civil que quer usar a tecnologia para aproximar a Populao do universo da Poltica brasileira.',
        ]},
        {q: 'O que  o lbum dos Polticos?', a: [
            ' um lbum 100% online e gratuito que transforma os partidos e os deputados federais em figurinhas para voc colecionar e trocar!',
            'Queremos que voc se divirta e conhea melhor os +20 partidos e +500 deputados federais que atuam hoje no Brasil.',
            'Usamos os dados do Ranking dos Polticos para ordenar o lbum do melhor para o pior partido, do melhor para o pior deputado federal.',
        ]},
        {q: 'Como ganhar novas figurinhas?', a: [
            'Quando cria um login, voc recebe um lbum vazio e 40 pacotinhos, cada um com 5 figurinhas.',
            'A cada 20 minutos, voc pode pegar 2 pacotinhos de graa.',
            'Troque suas figurinhas repetidas com amigos ou jogue e ganhe mais pacotinhos ;)'
        ]},
        {q: 'O que  o nmero que aparece nas figurinhas?', a: [
            'Mostramos qual a posio do deputado no Ranking dos Polticos e uma cor para indicar se ele est no topo ou em uma posio ruim no ranking.',
            'Por exemplo, a deputada Adriana Ventura, do Partido Novo de SP,  a #1 (primeira colocada) no Ranking dos Polticos.'
        ]},
        {q: 'De onde vm as notas?', a: [
            'Todas as avaliaes apresentadas pelo Polis vm do Ranking dos Polticos. Recomendamos que voc acesse o Ranking dos Polticos para se aprofundar no desempenho de cada parlamentar da esfera nacional.'
        ]},

    ]

    return <VStack alignItems={'start'} p='5' spacing='10'>
        {questions.map(question => {
            return <Question q={question.q} a={question.a}/>
        })}
    </VStack>
}

function Question({q, a} : {q: string, a: string[]}) {
    
    return <VStack spacing='2' alignItems={'start'}>
        <Heading size='sm'>{q}</Heading>
        {a.map(t => {
            return <Text fontSize='sm'>{t}</Text>
        })}
    </VStack>
}